<template>
  <div class="auth-callback">
    <div class="callback-container">
      <div class="callback-content">
        <div class="loading-spinner-large"></div>
        <h2 class="callback-title">{{ title }}</h2>
        <p class="callback-message">{{ message }}</p>
        <div v-if="showDebug" class="debug-info">
          <h4> :</h4>
          <pre>{{ debugInfo }}</pre>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex'

export default {
  name: 'AuthCallback',
  data() {
    return {
      title: '  ...',
      message: ' .',
      showDebug: false,
      debugInfo: {}
    }
  },
  async mounted() {
    console.log(' AuthCallback  ')
    // console.log('  URL:', window.location.href)
    // console.log('  :', this.$route)
    
    await this.handleAuthCallback()
  },
  methods: {
    ...mapActions('auth', ['loginWithToken']),
    
    async handleAuthCallback() {
      try {
        console.log(' OAuth   ')
        
        const params = new URLSearchParams(window.location.search)
        const token = params.get('token') || this.$route.query.token
        const provider = params.get('provider') || this.$route.query.provider
        const name = params.get('name') || this.$route.query.name
        const error = params.get('error') || this.$route.query.error
        
        this.debugInfo = {
          url: window.location.href,
          queryParams: this.$route.query,
          urlParams: Object.fromEntries(params),
          token: token ? token.substring(0, 20) + '...' : 'null',
          provider,
          name: name === ' ' ? '' : (name ? decodeURIComponent(name) : 'null'),
          error
        }
        
        // console.log('  :', this.debugInfo)
        
        //   
        if (error) {
          throw new Error(`OAuth : ${error}`)
        }
        
        //   
        if (!token) {
          this.showDebug = true
          throw new Error(' . URL  .')
        }
        
        // console.log('  :', token.substring(0, 30) + '...')
        console.log(' OAuth :', provider)
        console.log('  :', decodeURIComponent(name || ''))
        
        this.message = '  ...'
        
        //   
        const success = await this.loginWithToken(token)
        
        if (success) {
          console.log('  !  ')
          this.title = ' !'
          this.message = ' ...'
          
          // 2   
          setTimeout(() => {
            this.$router.replace('/')
          }, 2000)
        } else {
          throw new Error('  .')
        }
        
      } catch (error) {
        console.error(' Auth callback :', error)
        
        this.title = ' '
        this.message = `: ${error.message}`
        this.showDebug = true
        
        // 5    
        setTimeout(() => {
          this.$router.replace({
            name: 'Login',
            query: { error: 'callback_failed' }
          })
        }, 5000)
      }
    }
  }
}
</script>

<style scoped>
