  min-height: 100vh;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
}

.callback-container {
  text-align: center;
  color: white;
  width: 100%;
  max-width: 500px;
}

.callback-content {
  background-color: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border-radius: 16px;
  padding: 48px 32px;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.loading-spinner-large {
  width: 60px;
  height: 60px;
  border: 4px solid rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  border-top-color: white;
  animation: spin 1s ease-in-out infinite;
  margin: 0 auto 24px;
}

.callback-title {
  font-size: 24px;
  font-weight: 500;
  margin-bottom: 16px;
}

.callback-message {
  font-size: 16px;
  opacity: 0.9;
  margin-bottom: 24px;
  line-height: 1.5;
}

.debug-info {
  background-color: rgba(0, 0, 0, 0.3);
  border-radius: 8px;
  padding: 16px;
  margin-top: 20px;
  text-align: left;
}

.debug-info h4 {
  margin: 0 0 10px 0;
  font-size: 14px;
}

.debug-info pre {
  margin: 0;
  font-size: 12px;
  white-space: pre-wrap;
  word-break: break-all;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

@media (max-width: 600px) {
  .callback-content {
    padding: 32px 20px;
  }
  
  .callback-title {
    font-size: 20px;
  }
  
  .callback-message {
    font-size: 14px;
  }
}
</style>